<template>
  <div class="items">
    <h2>视频详情页</h2>
    <p>{{pageId}}</p>
  </div>
</template>
<script>
export default {
  data () {
    return {
      pageId: ''
    }
  },
  beforeRouteEnter (to, from, next) {
    console.log(to, from, next, 'test===')
    next(vm => {
      vm.pageId = to.query.id
    })
  }
}
</script>



